import React from 'react';
import { graphql } from 'gatsby';
import Img from 'gatsby-image';
import { Query } from '../../graphql-types';
import { Layout } from '../components/layout';
import { SEO } from '../components/seo';
import { Separator } from '../components/separator';
import { BreadCrumbs } from '../components/breadcrumbs';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { FormattedMessage, useIntl } from 'gatsby-plugin-intl';

const NewsPage: React.FunctionComponent<{ data: Query }> = ({ data }) => {
  const intl = useIntl();
  const { allFile } = data;
  const taos_1 = allFile.edges.find((v) => v.node.name === 'taos_1').node.childImageSharp.fluid;
  const taos_2 = allFile.edges.find((v) => v.node.name === 'taos_2').node.childImageSharp.fluid;
  const taos_3 = allFile.edges.find((v) => v.node.name === 'taos_3').node.childImageSharp.fluid;

  function renderMailTo() {
    return (
      <Container>
        <Row>
          <Col>
            <a className="btn btn-primary" href="mailto:info@aviaco-gse.com">
              Get More Info
            </a>
          </Col>
        </Row>
      </Container>
    );
  }
  function renderImages() {
    return (
      <Row>
        <Col md="4" sm="12">
          <Img fluid={taos_1} className="img-fluid" alt="AVIACO 6" />
        </Col>
        <Col md="4" sm="12">
          <Img fluid={taos_2} className="img-fluid" alt="AVIACO 9" />
        </Col>
        <Col md="4" sm="12">
          <Img fluid={taos_3} className="img-fluid" alt="AVIACO 15" />
        </Col>
      </Row>
    );
  }
  function renderContent(locale: string) {
    switch (locale) {
      case 'fr':
        return (
          <Container>
            {renderImages()}
            <hr />
            <Row>
              <Col>
                <p>TAOS (ASSISTANCE TECHNIQUE SUR SITE)</p>
                <p>
                  Le nouveau service d'AVIACO pour aider nos clients à redémarrer ses activités après Covid-19.
                  <br />
                  Des années d'expérience sur GSE permettent à notre équipe de fournir une évaluation complète et un service de révision
                  totale de votre flotte, quelle que soit sa taille.
                  <br />
                  Nous comptons avec tout le nécessaire pour vous accompagner dans l'entretien et la réparation de votre GSE où qu'il se
                  trouve:
                  <br />
                  <ul>
                    <li>Les meilleures installations d'atelier pour réparer, remettre à nouveau et entreposer votre équipement.</li>
                    <li>
                      Des techniciens de haut niveau avec une formation avancée et des connaissances sur les systèmes les plus complexes.
                    </li>
                    <li>
                      Des fourgons d'atelier entièrement équipés pour emmener notre personnel technique et toutes les pièces de rechange et
                      outils nécessaires sur votre site.
                    </li>
                  </ul>
                </p>
              </Col>
            </Row>
          </Container>
        );
      case 'es':
        return (
          <Container>
            {renderImages()}
            <hr />
            <Row>
              <Col>
                <p>TAOS (ASISTENCIA TECNICA IN SITU)</p>
                <p>
                  El nuevo servicio de AVIACO para ayudar a nuestros clientes a reiniciar sus actividades después del Covid-19.
                  <br />
                  Años de experiencia en GSE permiten a nuestro equipo proporcionar una evaluación completa y un servicio de revisión total
                  para su flota, independientemente de su tamaño.
                  <br />
                  Contamos con todo lo necesario para asistirlo en el mantenimiento y reparación de su GSE donde sea que se encuentre:
                  <ul>
                    <li>Las mejores instalaciones para reparar, reacondicionar y almacenar su equipo.</li>
                    <li>Técnicos de alto nivel con formación avanzada y conocimientos en los sistemas más complicados.</li>
                    <li>
                      Furgonetas taller totalmente equipadas para llevar a nuestro personal técnico y todos los repuestos y herramientas
                      necesarias a su ubicación.
                    </li>
                  </ul>
                </p>
              </Col>
            </Row>
          </Container>
        );
      default:
        return (
          <Container>
            {renderImages()}
            <hr />
            <Row>
              <Col>
                <p>TAOS (TECHNICAL ASSISTANCE ON SITE)</p>
                <p>
                  AVIACO's new service to support our customers to restart its activities after Covid-19. <br />
                  Years of experience on GSE allow our team to provide a full assessment and total overhaul service for your fleet,
                  regardless of its size.
                  <br />
                  We count with all the necessary to assist you in maintaining and repairing your GSE wherever they are located:
                  <br />
                  <ul>
                    <li>The best workshop facilities to repair, refurbish and store your equipment. </li>
                    <li>High-level technicians with advanced training and knowledge about the most complicated systems. </li>
                    <li>
                      Fully equipped workshop vans to take our technical staff and all the necessary spare parts and tools to your location.
                    </li>
                  </ul>
                </p>
              </Col>
            </Row>
          </Container>
        );
    }
  }

  return (
    <Layout>
      <SEO
        title={intl.formatMessage({ id: 'news-seo-title' })}
        description={intl.formatMessage({ id: 'news-seo-description' })}
        keywords={intl.formatMessage({ id: 'news-seo-keyword' })}
      />

      <main role="main">
        <article>
          <header className="page-header">
            <Container>
              <Row>
                <Col>
                  <BreadCrumbs section="whatsnew" />
                  <h1 className="page-title">
                    <FormattedMessage id="whats-new" />
                  </h1>
                  <p className="page-description">
                    <FormattedMessage id="whats-new-description" />
                  </p>
                  <Separator />
                </Col>
              </Row>
            </Container>
          </header>
          {renderContent(intl.locale)}
          {renderMailTo()}
        </article>
      </main>
    </Layout>
  );
};

export const query = graphql`
  {
    allFile(filter: { name: { in: ["taos_1", "taos_2", "taos_3"] } }) {
      edges {
        node {
          name
          childImageSharp {
            id
            fluid {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }
  }
`;

export default NewsPage;
